import Vue from "vue";
import {
    brigeNativeByJavascript
} from '../api/Bridge';
import { callBackNative } from '../api/axios.js'
import store from '../store'

const commonFunction = {
    openWin: (url, title, isLogin) => {
        const user = localStorage.user
        if (user) {
            if (!JSON.parse(user).isLogin && isLogin) {
                // brigeNativeByJavascript('pushNewApplication', {
                //     url: location.origin + '/login',
                //     title: '登录'
                // })
                // return
            }
        }
        if(url === '/login'){
            callBackNative('getSignInfo').then(data => {
                const json = JSON.parse(data)
                if(json.phone){
                    store.commit('setUser', { phone: json.userPhone, isLogin: true })
                }
            })
            return
        }
        brigeNativeByJavascript('pushNewApplication', {
            url: location.origin + url,
            title
        })
    }
}

for (const key in commonFunction) {
    Vue.prototype[key] = commonFunction[key]
}
