<template>
    <div class="home">
        <img class="banner" :src="appConfig.home.banner" alt>
        <div class="block main-block">
            <p class="title text-center">借款金额（元）</p>
            <p class="num text-center" @click="openSelect">
                {{selectMoney}}
                <span
                    class="jiao"
                    :style="{'border-left-color': appConfig.appColor}"
                ></span>
            </p>
            <hr>
            <p class="intro text-center">日息最低0.06%</p>
            <div class="text-center">
                <cube-button
                    class="code-btn"
                    :style="[appConfig.mainBackground]"
                    @click="goMoney"
                    :primary="true"
                >我要借钱</cube-button>
            </div>
            <p class="intro intro-xi text-center">- 借款有风险, 请根据实际还款能力进行借款 -</p>
        </div>
        <div class="block sm-block flex">
            <div class="flex-1 text-center" @click="openWin('/myCoupon', '我的优惠券')">
                <img :src="appConfig.home.ticket" alt>
                <div>我的优惠券</div>
            </div>
            <div class="flex-1 text-center" @click="openWin('/question', '常见问题')">
                <img :src="appConfig.home.question" alt>
                <div>常见问题</div>
            </div>
        </div>
    </div>
</template>

<script>
// @ is an alias to /src
import { mapState } from 'vuex'
import { brigeNativeByJavascript } from '../api/Bridge.js'

export default {
    name: 'home',
    data() {
        return {
            selectMoney: 10000,
            money: [
                { value: 3000, text: 3000 },
                { value: 5000, text: 5000 },
                { value: 10000, text: 10000 },
                { value: 20000, text: 20000 },
                { value: 30000, text: 30000 },
                { value: 50000, text: 50000 }
            ]
        }
    },
    computed: {
        ...mapState(['appConfig', 'user'])
    },
    methods: {
        openSelect() {
            this.picker = this.$createPicker({
                title: '借款金额',
                data: [this.money],
                onSelect: (selectedVal, selectedIndex, selectedText) => {
                    this.selectMoney = selectedVal[0]
                },
                onCancel: () => {}
            }).show()
        },
        goMoney() {
            if (!this.user.isLogin) {
                this.openWin('/login', '登录')
                return
            }
            if (this.user.selMoney.money) {
                this.openWin(
                    `/loanWaiting?money=${this.user.selMoney.money}&time=${this.user.selMoney.time}`,
                    '借款审核中'
                )
                return
            }
            this.openWin(`/loanCalc?money=${this.selectMoney}`, '贷款计算')
            return
            // if (this.user.certification.person && this.user.certification.img && this.user.certification.bank) {
            // 	this.openWin(`/loanCalc?selectMoney=${this.selectMoney}`, '贷款计算')
            //         return
            // } else {
            // 	this.openWin('/creditCertification', '信用认证')
            //         return
            // }
        }
    },
    mounted() {
        if (!localStorage.user) {
            this.$store.commit('setUser', {})
        }
    }
}
</script>
