<template>
    <div id="app">
        <router-view id="child-route"/>
        <div class="refresh" @click="refresh" v-if="isRefresh"></div>
    </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
    data() {
        return {
            isRefresh: false
        }
    },
    methods: {
        refresh() {
            location.reload()
        },
        getStorage() {
            var user = JSON.parse(localStorage.user)
            // alert(123)
            this.$store.commit('setUser', user)
        }
    },
    computed: {
        ...mapState(['user'])
    },
    created() {
        window.getStorage = () => {
            // alert(233)
            this.getStorage()
        }
    },
    mounted() {
        this.getStorage()
        if (process.env.NODE_ENV == 'development') {
            this.isRefresh = true
        }
    }
}
</script>

<style lang="scss">
#app {
    font-size: 14px;
    background-color: #f5f5f5;
    // overflow: hidden;
    // min-height: 100%;
    min-height: 100%;
    position: relative;
    #child-route {
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        overflow: auto;
    }
    .refresh {
        position: fixed;
        top: 5px;
        left: 5px;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        background: #fff;
        opacity: 0.2;
    }
}
</style>
