//POST传参序列化
axios.interceptors.request.use(
    config => {
        if (config.method === 'post') {
            config.data = qs.stringify(config.data)
        }
        return config
    },
    error => {
        Toast('错误的传参')
        return Promise.reject(error)
    }
)

//返回状态判断
axios.interceptors.response.use(
    res => {
        store.commit('loading', false)
        if (res.status !== 200) {
            return Promise.reject(res)
        } else if (res.data.status === 'error') {
            Toast(res.data.data)
            return Promise.reject(res)
        }
        if (res.data && res.data.code && res.data.code != 0) {
            if (res.data.msg) Toast(res.data.msg)
        }
        return res
    },
    error => {
        store.commit('loading', false)
        Toast('网络异常')
        return Promise.reject(error)
    }
)

export function fetch(url, params = {}, method = 'post') {
    return new Promise((resolve, reject) => {
        console.log(params)
        axios[method](url, params)
            .then(
                response => {
                    resolve(response.data)
                },
                err => {
                    reject(err)
                }
            )
            .catch(error => {
                reject(error)
            })
    })
}

export function fetchHeader(url, params = {}, method = 'post', headers) {
    return new Promise(async (resolve, reject) => {
        if (headers === 'test') headers = testHeaders
        else if (localStorage.headersInfo) {
            headers = JSON.parse(localStorage.headersInfo)
        } else {
            headers = await callBackNative('getSignInfo')
            headers = JSON.parse(headers)
        }
        console.log(headers)
        axios({
                url,
                params,
                method,
                headers: headers
            })
            .then(
                response => {
                    resolve(response.data)
                },
                err => {
                    reject(err)
                }
            )
            .catch(error => {
                reject(error)
            })
    })
}

// 有callback的调用原生方法
export const callBackNative = (funcName, args) => {
    return new Promise((resolve, reject) => {
        const date = new Date().getTime()
        window['nativeFuncName' + date] = data => {
            resolve(data)
        }
        brigeNativeByJavascript(funcName, args, window['nativeFuncName' + date], 'nativeFuncName' + date)
    })
}