function setupWebViewJavascriptBridge(callback) {
  if (window.WebViewJavascriptBridge) {
    return callback(WebViewJavascriptBridge);
  }
  if (window.WVJBCallbacks) {
    return window.WVJBCallbacks.push(callback);
  }
  window.WVJBCallbacks = [callback];
  let WVJBIframe = document.createElement('iframe');
  WVJBIframe.style.display = 'none';
  WVJBIframe.src = 'wvjbscheme://__BRIDGE_LOADED__';

  document.documentElement.appendChild(WVJBIframe);
  setTimeout(function () {
    document.documentElement.removeChild(WVJBIframe)
  }, 0)
}

//  重新封装的方法 - shengchao
export const brigeNativeByJavascript = (funcName, data, callbackName, callbackNameStr) => {
  if (window.android) {
    console.log(window.android);
    let dataJson = data ? JSON.stringify(data) : '{}'
    if (callbackName) {
      window.android[funcName](dataJson, callbackNameStr)
    } else {
      if (funcName === 'closeRefundApplication') {
        window.android[funcName]()
      }
      window.android[funcName](dataJson)
    }

  } else {
    setupWebViewJavascriptBridge(brige => {
      brige.callHandler(funcName, data, callbackName)
    })
  }
}