import Vue from "vue";
import Vuex from "vuex";
import {
    Toast
} from 'cube-ui';
import appConfig from './plugins/appConfig';

Vue.use(Vuex);
Vue.use(Toast);

let loading = Toast.$create({
    txt: 'Loading...',
    time: 0,
    mask: true
})

export default new Vuex.Store({
    state: {
        msg: ' ',
        user: {
            phone: '',
            isLogin: false,
            certification: {
                person: false,
                img: false,
                bank: false
            },
            selMoney: {
                money: '',
                time: ''
            }
        },
        certification: {
            person: true,
            img: false,
            bank: false
        },
        appConfig: {
            mainColor: {
                color: appConfig.appColor
            },
            mainBackground: {
                'background-color': appConfig.appColor
            },
            ...appConfig
        },
        loading: false
    },
    mutations: {
        setUser(state, payload) {
            if (typeof (payload) != 'object') {
                alert('设置错误')
                return
            }
            state.user = { ...state.user,
                ...payload
            }
            localStorage.user = JSON.stringify(state.user)
        },
        setMsg(state, payload) { // Toast
            if (state.loading) return
            if (typeof (payload) === 'string') {
                loading = Toast.$create({
                    txt: payload,
                    type: 'txt'
                })
            } else {
                loading = Toast.$create(payload)
            }
            loading.show()
            state.msg = payload;
        },
        loading(state, payload) {
            if (payload && state.loading) return
            state.loading = payload
            loading = Toast.$create({
                txt: 'Loading...',
                time: 0,
                mask: true
            })
            if (payload) loading.show()
            else loading.hide()
        }
    },
    actions: {}
});