import Vue from "vue"
import Router from "vue-router"
import Home from "./views/Home.vue"

Vue.use(Router);

const PathChunk = path => {
  return {
    path: "/" + path,
    name: path,
    component: () =>
      import( /* webpackChunkName: "[request][index]" */ `./views/${path}.vue`)
  }
}

const Route = new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [{
      path: "/",
      name: "home",
      component: Home
    },
    PathChunk("login"),
    PathChunk("agreement"),
    PathChunk("loanAgreement"),
    PathChunk("about"),
    PathChunk("myCoupon"),
    PathChunk("loanCalc"), // 贷款计算
    PathChunk("loanCalcConfirm"), // 借款确认
    PathChunk("loanWaiting"), // 借款审核中
    PathChunk("creditCertification"), // 信用认证 按顺序认证
    PathChunk("idCardAuth"), // 身份证认证 拍照上传
    PathChunk("qiyu"), // 在线客服
    PathChunk("question"),
    PathChunk("Udcredit"),
    PathChunk("billRecord"),
    PathChunk("billDetails"),
    PathChunk("personCenter"),
    PathChunk("personInfo"),
    PathChunk("personContact"),
    PathChunk("bankAuth"),
    PathChunk("bankList"),
    PathChunk("bankCardList"),
    PathChunk("payDetails"),
    PathChunk("policy"),
  ]
});

export default Route
